import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ShortFallRefurbComponent } from './short-fall-refurb.component';


const routes: Routes = [
  {
    path: '',
    component: ShortFallRefurbComponent,
    data: {
      title: 'ShortFall'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ShortFallRefurbRoutingModule { }
